.search-form {
  position: relative;
  background-color: $base-color;
  max-width: myrem(456);
  border-radius: 20px;
  transition: .3s;
  box-shadow:6px 6px 20px #e3e3e3,
             -6px -6px 10px #ffffff,
             inset -2px -2px 3px #A2A2A257;
  border: none;
  &:focus,&:hover {
    box-shadow:
    10px 10px 20px #e3e3e3,
    -10px -10px 20px #ffffff,
    inset 5px 5px 3px #A2A2A257;
  }

  &::before {
    content: "";
    display: inline-block;
    width: myrem(19);
    height: myrem(19);
    background: url(../img/common/icon-search-green.svg) no-repeat center center /contain;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: myrem(16);
    z-index: 2;
  }

  @media #{$sp} {
    margin-inline: auto;
  }
}

.search-form__text{
	box-sizing: border-box;
  background-color: transparent;
  border-radius: 20px;
  padding: 3px 10px;
  height: 2.3em;
  width: 260px;
  overflow: hidden;
  font-size: myrem(13);
  padding: myrem(15) myrem(25);
  color: #9F9F9F;
  min-height: myrem(36);
  width: 100%;

  &:focus,&:hover {
  outline: none;
  border: none;
  }
}
