// Breakpoint

$sp: "all and (max-width : 768px)";
//$sm: "all and (max-width : 840px)";
$md: "all and (max-width : 1000px)";
//$lg: "all and (max-width : 1100px)";
$tab: "all and (max-width : 860px)";
//$xl: "all and (max-width : 1200px)";
$pc: "all and (min-width: 769px)";

// 
$font-family: 'Noto Sans JP',' Pro W3','Hiragino Kaku Gothic Pro','','Yu Gothic','','YuGothic',' ','MS PGothic',sans-serif;


// 
$primary-color: #000;
$secondary-color: #ccc;
$tertiary-color: #eee;
$base-color: #F7F7F7;

$black: #000;
$white: #fff;
$darkgreen: #32743D;
$lightgreen: #7ACC88;
$lightgreen2: #69AC74;
$yellow: #D8BA31;
$lightblue: #70CEEE;
$orange: #EE8D70;

$grade01: transparent linear-gradient(103deg, #70CCF5 0%, #72DBA2 20%, #7ACC88 83%, #C8BE40 92%, #DBBA2E 100%) 0% 0% no-repeat padding-box;


// 
$container: 1440px;
$container-full: 100%;

// 
$gutter: 20px;
$small-gutter: 10px;

// 
$shadow:
7px 7px 14px #d9d9d9,
-7px -7px 14px #ffffff,
-3px -3px 5px #ffffff;

$shadow--sm:
7px 4px 10px #d9d9d9,
-7px -7px 14px #ffffff;

$shadow-hover:
2px 2px 6px #d9d9d9,
-2px -2px 6px #ffffff,
6px 6px 10px rgba(0, 0, 0, .3);



